import React from "react";
import "hui/single-step";

export default class HASingleStep extends React.Component {
    static propTypes = {
        id: React.PropTypes.string,
        className: React.PropTypes.string,
        showOnRender: React.PropTypes.bool,
        targetSelector: React.PropTypes.string,
        position: React.PropTypes.string,
        titleText: React.PropTypes.string,
        message: React.PropTypes.string,
        dismissible: React.PropTypes.bool,
        trigger: React.PropTypes.string,
        duration: React.PropTypes.oneOfType([React.PropTypes.number, React.PropTypes.string]),
        onChange: React.PropTypes.func,
        onClick: React.PropTypes.func
    };

    constructor(props) {
        super(props);
        this._huiComponent = null;
        this._listeners = {
            onChange: null
        };
    }

    //Event handler for Click
    onClick = (event) => {
        if (this.props.onClick) {
            this.props.onClick(event);
        }
    };

    componentDidMount() {
        // Event handler for change
        this._listeners.onChange = (event) => {
            event.stopPropagation();
            if (this.props.onChange) {
                this.props.onChange(event);
            }
        };
        this._huiComponent.addEventListener("change", this._listeners.onChange);

    }

    componentWillUnmount() {
        this._huiComponent.removeEventListener("change", this._listeners.onChange, false);
    }

    handleRef = (c) => {
        this._huiComponent = c;
    };

    render() {
        /* jshint ignore:start */
        return (
            <ha-single-step
                ref={this.handleRef}
                class={this.props.className}
                onClick={this.onClick}
                {...this.props}>
                {this.props.children}
            </ha-single-step>
        );
        /* jshint ignore:end */
    }
}
